import Vue from 'vue'
import Router from 'vue-router'
import MyMain from '@/components/main'                  //
import Login from '@/components/login'                  //
import Writeorder from '@/components/writeorder'        //
import Productdec from '@/components/productdec'        //
import Productlist from '@/components/productlist'      //
import Car from '@/components/car'                      //
import My from '@/components/my'                        //
import Ordergive from '@/components/ordergive'          //
import Orderok from '@/components/orderok'              //
import Xgtj from '@/components/xgtj'                    //
import Myhf from '@/components/myhf'                    //

Vue.use(Router);

export default new Router({
    routes: [
        //    
        {
            path: '/login',
            name: 'login',
            component: Login
        },
        {
            path:'/',
            name:'myhf',
            component:Myhf,
            redirect:'mymain',
            children: [
                //   
                {
                    path: '/writeorder',
                    name: 'writeorder',
                    component: Writeorder
                },
                //   
                {
                    path: '/ordergive',
                    name: 'ordergive',
                    component: Ordergive
                },
                //
                {
                    path: '/orderok',
                    name: 'orderok',
                    component: Orderok
                },
                //   
                {
                    path: '/productdec',
                    name: 'productdec',
                    component: Productdec
                },
                //   
                {
                    path: '/productlist',
                    name: 'productlist',
                    component: Productlist
                },
                //   
                {
                    path: '/car',
                    name: 'car',
                    component: Car
                },
                //   
                {
                    path: '/my',
                    name: 'my',
                    component: My
                },
                //   
                {
                    path: '/xgtj',
                    name: 'xgtj',
                    component: Xgtj
                },
                //
                {
                    path: '/mymain',
                    name: 'mymain',
                    component: MyMain,
                },
            ]
        }
    ]
})



